import mart from './mart.js'
import martDetail from './martDetail.js'
import news from './news.js' // 1 

const json = {
	mart,
	martDetail,
	news, // 2 [finish mock api]
}

export default (name, Loading = true) => {
	if (Loading) {
		uni.showLoading()
	}
	return new Promise(resolve => {
		setTimeout(() => {
			uni.hideLoading()
			resolve(json[name])
		}, 500)
	})
}